@import "variables.scss";

.footer{
  max-width: 1920px;
  background-color: $color-pink;
  margin: 90px auto 0 auto;

  &-container {
    max-width: 1170px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 25px 0px 40px 0px;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }
}